<template>
   <div>
       <v-layout
               class="mb-6 mt-1 hidden-sm-and-down"
               no-gutters
       >
           <v-flex xs3 sm3 md3 lg3>
               <app-user
                          :fetchOrders="fetchOrders"
                          :orders="orders"
                          :auth="auth"
                          :currentOrder="currentOrder"
               ></app-user>
           </v-flex>
           <v-flex>
               <router-view/>
           </v-flex>
           <v-flex xs3 sm3 md3 lg3>
               <app-status
                       :currentOrder="currentOrder"
                       :auth="auth"
               ></app-status>
           </v-flex>
       </v-layout>
       <div class="hidden-md-and-up">
           <v-img src="https://i.pinimg.com/originals/65/9c/fe/659cfe8fc0d9b168b721e90690e464c3.png"></v-img>
       </div>
   </div>
</template>

<script>
    import appUser from '../../components/message/User'
    import appStatus from '../../components/message/Status'
    import { mapActions, mapGetters } from 'vuex'
    import io from 'socket.io-client'
    const socket = io('http://localhost:1307')

    export default {
        components: {
            appUser,
            appStatus
        },
        data: () => ({
           orders: [],
        }),
        computed: {
          ...mapGetters([
              'auth',
              'currentOrder'
          ])
        },
        methods: {
            ...mapActions([
                'fetchCurrentOrder'
            ]),
             async fetchOrders () {
             const results = await this.$axios.get('order/orders', { params: {
                 authId: this.auth._id
                 }})
                 this.orders =  results.data.orders
            }
        },
       created () {
            this.fetchOrders()
            this.fetchCurrentOrder(this.$route.params.id)
        },
        mounted () {
           // const { _id, employerId } = this.currentOrder
                socket.on('update-quotation', () => this.fetchCurrentOrder(this.$route.params.id))
                socket.on('create-order', () => this.fetchOrders())
            }
    }
</script>

