<template>
    <v-row justify="center">
        <v-dialog v-model="dialog" persistent max-width="600px">
            <template v-slot:activator="{ on }">
                <v-btn block class="primary" v-on="on">ສ້າງໃບສະເໜີລາຄາ</v-btn>
            </template>
            <v-card>
                <v-form @submit.prevent="updateQuotation">
                <v-card-title>
                    <span class="headline">ສ້າງໃບສະເໜີລາຄາ</span>
                </v-card-title>
                <v-card-text>
                    <v-container>
                            <v-text-field
                                    v-model="quotation.price"
                                    label="ລາຄາ*"
                                    type="number"
                                    outlined
                                    required
                            ></v-text-field>
                            <v-textarea
                                    v-model="quotation.information"
                                    outlined
                                    label="ລາຍລະອຽດ*"
                                    type="text"
                                    required
                            ></v-textarea>
                            <v-text-field
                                    v-model="quotation.dayWork"
                                    outlined
                                    label="ເວລາໃນການເຮັດວຽກ*"
                                    type="number"
                                    required
                            ></v-text-field>
                            <v-text-field
                                    v-model="quotation.receive"
                                    outlined
                                    label="ສິ່ງທີ່ລູກຄ້າຈະໄດ້ຮັບ*"
                                    type="text"
                                    required
                            ></v-text-field>
                            <v-textarea
                                    v-model="quotation.optional"
                                    outlined
                                    label="ໝາຍເຫດ*"
                                    type="text"
                                    required
                            ></v-textarea>
                    </v-container>
                    <small>*ກະລຸນາປ້ອນຂໍ້ມູນໃຫ້ຄົບຖ້ວນ</small>
                </v-card-text>
                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn color="red darken-1" text @click="dialog = false">ປິດ</v-btn>
                    <v-btn type="submit" color="primary"  @click="dialog = false">ສ້າງໃບສະເໜີລາຄາ</v-btn>
                </v-card-actions>
                </v-form>
            </v-card>
        </v-dialog>
    </v-row>
</template>

<script>
    import { mapActions } from 'vuex'
    import io from 'socket.io-client'
    const socket = io('http://localhost:1307')
    export default {
        props: [
            'auth',
            'currentOrder'
        ],
        data: () => ({
            dialog: false,
            quotation: {
                information: '',
                price: '',
                dayWork: '',
                receive: '',
                optional: ''
            }
        }),
        methods: {
            ...mapActions([
                'fetchCurrentOrder'
            ]),
            async updateQuotation () {
                try {
                    const { information, price, dayWork, receive, optional } = this.quotation
                    await this.$axios.put('order/update-quotation', {
                        _id: this.currentOrder._id,
                        information,
                        price,
                        dayWork,
                        receive,
                        optional,
                        // message
                        message: 'ໃບສະເໜີລາຄາ',
                        type: 'quotation',
                        from: this.currentOrder.freelancerId._id,
                        to: this.currentOrder.employerId._id,
                        orderId: this.currentOrder._id
                    })
                    await this.fetchCurrentOrder(this.currentOrder._id)
                    // emit io
                    socket.emit(`update-quotation`)
                    socket.emit('update-message')
                    this.$swal({
                        title: 'ສ້າງໃບສະເໜີລາຄາສໍາເລັດ',
                        type: 'success'
                    })
                } catch (e) {
                    throw new Error(e)
                }
            }
        },
    }
</script>
